<template>
    <div id="setPassword">
        <mt-header title="设置密码" style="height:.88rem;background-color:#4a57ff">
            <mt-button slot="left" icon="back" @click="goBack"></mt-button>
        </mt-header>
        <div class="content">
            <div class="title">请设置6位纯数字交易密码</div>
            <div class="pswContent">
                <div class="item_psw">
                    <div class="laber">密码</div>
                    <input type="password" :maxlength="6" v-model="password" @keyup="changePsw($event)">
                </div>
                <div class="item_psw" style="margin-top: .4rem;">
                    <div class="laber">确认密码</div>
                    <input type="password" :maxlength="6" v-model="password1">
                </div>
                <div class="tips" v-if="tipsShow">两次密码输入不一致，请重新输入</div>
            </div>
            <mt-button class="submit" type="primary" @click="submit" :disabled="password&&password1?false:true">确定</mt-button>
        </div>
    </div>
</template>
<script>
import { encrypt } from '@/utils/index.js'
import { changePassword } from '@/api/withdraw.js'
import { Toast } from 'mint-ui';
export default {
    data() {
        return {
            password:'',
            password1:'',
            tipsShow:false
        }
    },
    created() {
    },
    methods:{
        goBack() {
            this.$router.go(-1)
        },
        changePsw(e) {
            e.target.value = e.target.value.replace(/\D/g,'')
            this.password = e.target.value
        },
        submit() {
            if(this.password.length!=6){
                Toast('请设置6位纯数字交易密码')
                return false
            }
            if(this.password != this.password1) {
                this.tipsShow = true   
                this.password = ''
                this.password1 = ''
                return false  
            }else{
                let password = encrypt(this.password)
                // password = encodeURIComponent(password)
                let confirm_password = password
                let temp_token = this.$route.params.temp_token
                changePassword({password,confirm_password,temp_token}).then(data => {
                    if(data.code===0){
                        Toast('设置成功')
                        this.$router.go(-2)
                    }else {

                    }
                })
                this.tipsShow = false   
            }
            
        }
    }
}
</script>
<style lang="less" scoped>
#setPassword{
    width: 100%;
    height: 13.34rem;
    background-color: #fafafa;
    .content{
        padding-top: 1.2rem;
        width: 100%;
        height: auto;
        .submit{
            width: 6.9rem;
            height: .8rem;
            margin: 1.24rem auto 0;
            border-radius: .1rem;
            font-size: .34rem;
            display: block;
            background-color: rgb(74, 87, 255);
        }
        .title{
            text-align: center;
            font-family: MicrosoftYaHei;
            font-size: .32rem;
            font-weight: normal;
            font-stretch: normal;
            letter-spacing: 0px;
            color: #666666;
        }
        .pswContent{
            margin-top: 1rem;
            width: 100%;
            height: 3rem;
            padding: 0 .3rem;
            .tips{
                text-align: center;
                margin-top: .2rem;
                font-family: MicrosoftYaHei;
                font-size: .26rem;
                font-weight: normal;
                font-stretch: normal;
                letter-spacing: 0rem;
                color: #ff0000;
            }
            .item_psw{
                width: 100%;
                height: .8rem;
                display: flex;
                align-items: center;
                justify-content: space-between;
                .laber{
                    width: 2rem;
                    font-family: MicrosoftYaHei;
                    font-size: .3rem;
                    font-weight: normal;
                    font-stretch: normal;
                    letter-spacing: 0px;
                    color: #000000;
                    text-align: left;
                }
                input{
                    width: 100%;
                    height: .8rem;
                    background-color: #ffffff;
                    border-radius: .1rem;
                    border: solid .02rem #e5e5e5;
                    padding-left: .4rem;
                    font-size: .3rem;
                }
            }
        }
    }
}
</style>



